<template>
  <div class="header">
    <ul class="header-button-left">
      <li v-if="step != 3" @click="step=3">Follower</li>
      <li v-if="step == 3" @click="step=0">Back</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="step == 1" @click="step++">Next</li>
      <li v-if="step == 2" @click="publish();">Publish</li>
    </ul>
    <img src="./assets/logo.png" class="logo" />
  </div>
  
  <!-- <Container :filterName="filterName" v-bind:instaData="$store.state.instaData" :step="step" :url="uploadedFileUrl" @contentFromContainer="contentFromContainer = $event"/> -->
  <Container :filterName="filterName" :step="step" :url="uploadedFileUrl" @contentFromContainer="contentFromContainer = $event"/>
  <!-- <button @click="more">더보기</button> -->
  <!-- <button @click="$store.dispatch('getData')">더보기버튼</button> -->


  <div class="footer">
    <ul class="footer-button-plus">
      <input @change="upload" type="file" id="file" class="inputfile" />
      <label for="file" class="input-plus">+</label>
    </ul>
  </div>



</template>

<script>
import Container from './components/Container.vue';
// import instaData from './assets/instaData';
import axios from 'axios';

export default {
  name: "App",
  data() {
    return {
      uploadedFileUrl: '',
      step: 0,
      count: 0,
      // instaData: instaData,
      contentFromContainer: '',
      filterName: ''
      
    }
  },
  mounted() {
    this.emitter.on('filterName', (a)=>{
      this.filterName = a
    })
  },
  components: {
    Container
  },
  methods: {
    more(){
      
      axios.get(`https://codingapple1.github.io/vue/more${this.count}.json`).then((result)=> {
        this.$store.state.instaData.push(result.data);
        this.count++;
        })     
    
    },
    upload(e){
      let uploadedFile = e.target.files;
      
      this.uploadedFileUrl = URL.createObjectURL(uploadedFile[0]);
      this.step = 1;

    },
    publish(){
      var myPost = {
      name: "Seojin Yeom",
      userImage: "https://placeimg.com/100/100/arch",
      postImage: this.uploadedFileUrl,
      likes: 360,
      date: "May 15",
      liked: false,
      content: this.contentFromContainer,
      filter: this.filterName
      };
      this.$store.state.instaData.unshift(myPost)
      this.step = 0;
    }
  }
}
</script>

