<template>
  <div style="padding: 10px">
    <div style="padding: 10px">
      <h4>팔로워</h4>
      <input @input="filterFollower" placeholder="?" />
      <div v-for="i in modFollower" :key="i" class="post-header">
        <div class="profile" :style="`background-image:url(${i.image})`"></div>
        <span class="profile-name">{{i.name}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted, } from "vue";
import axios from 'axios';

export default {
  setup(props) {
    let follower = ref([]);
    let modFollower = ref([]);

    function filterFollower(a){
        modFollower.value = [];
        for (var i in follower.value){
            if (follower.value[i].name.includes(a.target.value)){
                modFollower.value.push(follower.value[i])
            }
        }
    }

    onMounted(() => {
        axios.get('/follower.json').then((a)=>{
        follower.value = a.data;
        modFollower.value = a.data;
        
    });
    })
    return {follower, modFollower, filterFollower}
  },
};
</script>

